.homeContainer {
  display: flex;
  justify-content: center;
  width: 100%;
}

.homeList {
  width: 1184px;
  font-weight: 400;
  list-style: none;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  row-gap: 50px;
  column-gap: calc((1184px - 274px * 4) / 3);
  margin-inline-start: 0;
  padding-inline-start: 0;
  justify-content: center;
  margin-block-end: 0;
  margin-block-start: 0;
  margin-top: 70px;
}

.goBack {
  position: absolute;
  top: 70px;
  left: 5px;
  display: inline-flex;
  text-decoration: none;
  border: 1px solid #e6eaed;
  border-radius: 0 15px 15px 0;
  width: 70px;
  height: 15px;
  margin-block-end: 0;
  margin-block-start: 0;
  justify-content: center;
  align-items: center;
  color: #e6eaed;
  background-color: #af9e8e;
  font-size: 12px;
  transition: all 0.003s;
}
.goback:active,
.goback:hover,
.goback:focus {
  background-color: #b1b9cc;
  color: #af9e8e;
}
